<template><section><h1>GroundPolylinePrimitive</h1>
<p><code v-pre>vc-primitive-polyline-ground</code> 组件用于加载贴地或 3DTiles 图元，只能通过几何体实例 <code v-pre>vc-instance-geometry</code> 加载 <code v-pre>vc-geometry-polyline-ground</code>。</p>
<h2>示例</h2>
<h3>加载折线图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-polyline-ground :appearance="appearance" :geometryInstances="geometryInstances"></vc-primitive-polyline-ground>
        <vc-primitive-polyline-ground :appearance="appearance">
          <vc-instance-geometry>
            <vc-geometry-polyline-ground :positions="positions" :width="2"></vc-geometry-polyline-ground>
          </vc-instance-geometry>
        </vc-primitive-polyline-ground>
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-layer-imagery>
          <vc-provider-imagery-mapbox mapId="mapbox.streets"></vc-provider-imagery-mapbox>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-polyline-ground</span>
        <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>
        <span class="hljs-attr">:geometryInstances</span>=<span class="hljs-string">"geometryInstances"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-polyline-ground</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-polyline-ground</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-polyline-ground</span> <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions"</span> <span class="hljs-attr">:width</span>=<span class="hljs-string">"2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-polyline-ground</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-polyline-ground</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-mapbox</span> <span class="hljs-attr">mapId</span>=<span class="hljs-string">"mapbox.streets"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-mapbox</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: {},
        <span class="hljs-attr">geometryInstances</span>: {},
        <span class="hljs-attr">positions</span>: [{ <span class="hljs-attr">lng</span>: <span class="hljs-number">100.1340164450331</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.05494287836128</span> }, { <span class="hljs-attr">lng</span>: <span class="hljs-number">108.08821010582645</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.05494287836128</span> }]
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.geometryInstances = <span class="hljs-keyword">new</span> Cesium.GeometryInstance({
          <span class="hljs-attr">geometry</span>: <span class="hljs-keyword">new</span> Cesium.GroundPolylineGeometry({
            <span class="hljs-attr">positions</span>: Cesium.Cartesian3.fromDegreesArray([
